<template>
  <div>
    <div v-for="item in catalogArr" class="catalog-item" :key="item.id">
      <!--  -->
      <p :title="item.text" class="grade">
        <!--  -->
        <span
          @click="isOpen(item)"
          class="arrow iconfont"
          :class="
            !item.children.length
              ? 'no-show'
              : data.actNavOpen.includes(item.id)
              ? 'icon-zhedie'
              : 'icon-zhankai'
          "
        ></span>
        <!--  -->
        <span
          class="catalog-text"
          :class="{ high: data.actNavHigh === item.id }"
          @click="catalogLocation(item.id)"
        >
          {{ item.text }}
        </span>
      </p>
      <!--  data.actNavOpen.includes(item.id) -->
      <h-transitionH>
        <catalogNav
          v-show="data.actNavOpen.includes(item.id) !== isAllOpen"
          v-if="item.children.length"
          :onlyOpen="onlyOpen"
          :isAllOpen="isAllOpen"
          :catalogArr="item.children"
          @catalogId="catalogId"
        ></catalogNav>
      </h-transitionH>
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, onBeforeMount, onMounted } from 'vue';
export default {
  name: 'catalogNav',
  props: {
    // 
    catalogArr: {
      type: Array,
      default: () => {
        return [];
      }
    },
    // 
    onlyOpen: {
      type: Boolean,
      default: false
    },
    // 
    isAllOpen: {
      type: Boolean,
      default: false
    }
  },
  setup(props, ctx) {
    const data = reactive({
      actNavOpen: [], //id
      actNavHigh: '', //
      myOnlyOpen: props.isAllOpen ? false : props.onlyOpen   //
    });
    onBeforeMount(() => {});
    onMounted(() => {});
    //id
    const isOpen = (navObj) => {
      if (!navObj.children.length) return; //
      let navIndex = data.actNavOpen.indexOf(navObj.id); //id
      if (navIndex == -1) {
        if (data.myOnlyOpen) {
          data.actNavOpen = [navObj.id];
        } else {
          data.actNavOpen.push(navObj.id); //
        }
      } else {
        data.actNavOpen.splice(navIndex, 1); //
      }
    };

    // 
    const catalogLocation = (id) => {
      let ele = document.querySelector(`#${id}`);
      ele.scrollIntoView({
        // block : "center",   //
        behavior: 'smooth' // 
      });
      ctx.emit('catalogId', ele); //
    };
    // ele
    const catalogId = (ele) => {
      ctx.emit('catalogId', ele);
    };
    return {
      catalogLocation,
      isOpen,
      data,
      catalogId
    };
  }
};
</script>
<style scoped lang="less">
.catalog-item {
  cursor: pointer;
  font-size: 14px;
  padding: 2px;
  padding-left: 15px;
  .grade {
    overflow: hidden; /**/
    text-overflow: ellipsis; /*  */
    white-space: nowrap; /*  */
    .arrow {
      color: #999;
      &.no-show {
        padding-left: 16px;
      }
      &:hover {
        color: rgb(0, 125, 241);
      }
    }
    .catalog-text {
      padding-left: 5px;
      &.high {
        color: rgb(0, 125, 241);
      }
    }
  }
}
</style>
