<template>
  <!--  -->
  <div class="long-text-tpl">
    <div class="loading" v-show="data.isLoading">
      <p class="loading-icon iconfont icon-sousuo"></p>
      <p id="LoadAnimation"></p>
    </div>
    <!--  -->
    <div class="center" id="longCenter" v-html="data.longTextContent"></div>
  </div>
</template>
<script>
import { reactive, getCurrentInstance, onMounted, watch } from 'vue';
import Clipboard from 'clipboard';
import { RETRIEVAL_TYPE } from '../longText/longTextConst.js';
import { explainDoc } from '../longText/devDoc.js';

export default {
  components: {},
  props: {
    fileList: {
      type: Object
    },
    getFileList: {
      type: String
    },
    longKeyWordObj: {
      type: Object,
      default: () => {
        return {};
      }
    },
    switchHighObj: {
      type: Object
    },
    hierarchyNum: {
      type: Number
    }
  },
  setup(props, ctx) {
    const { proxy } = getCurrentInstance();
    const data = reactive({
      keyWordBCol: 'red', //
      keyWordSCol: '#fff', //
      keyWordBColRange: '#ccc', //
      keyWordSColRange: '#000', //
      keyPLength: 20, //
      catalogGrade: 0, // 0 
      // 
      RETRIEVAL_TYPE, //
      oldKeyEle: null, //
      oldPEle: null, //
      Knum: 0, //
      Ktotal: 0, //
      listNumId: 0, //id
      paragraphClickHigh: [], //  dom
      textCenter: '', // ()
      longTextCatalog: [], //
      longTextContent: '', // ()
      centerEle: null, // dom
      queryKeyWord: '', //      
      oldQueryKeyWordReg: '', //
      queryKeyWordArr: [], //
      queryKeyWordReg: '', //      |
      keyWordIndex: [], // 
      matchingCatalogue: [], //   matchingCatalogue
      matchingCatalogueP: [], //    matchingCatalogueP
      queryType: RETRIEVAL_TYPE.piece, // 0  1  2
      oldQueryType: '', // 
      copyButton: null, //
      isLoading: false, //
      isHaveKey: false, //
      oldPieceTargetEle: null, //  oldPieceTargetEle
      oldPieceTargetText: '', //  oldPieceTargetText
      oldPieceNum: '' //id              oldPieceNum
    });
    watch(
      // 
      () => props.getFileList,
      () => {
        // 
        data.catalogGrade = props.hierarchyNum; //
        generateText(props.getFileList); //
      }
    );
    watch(
      // 
      () => props.fileList,
      () => {
        // 
        data.catalogGrade = props.hierarchyNum; //
        choosefile(props.fileList); //
      }
    );
    watch(
      // 
      () => props.switchHighObj,
      () => {
        // 
        switchHighlight(props.switchHighObj.type, props.switchHighObj.index);
      },
      { deep: true }
    );
    watch(
      // 
      () => props.longKeyWordObj,
      () => {
        // 
        data.keyPLength =
          props.longKeyWordObj.interceptionNum < 0
            ? 0
            : props.longKeyWordObj.interceptionNum; //
        data.queryKeyWord = props.longKeyWordObj.queryKeyWord;
        data.queryType = props.longKeyWordObj.queryType;
        startQuery();
      },
      { deep: true }
    );
    watch(
      // 
      () => data.Knum,
      () => {
        ctx.emit('getKnum', data.Knum);
      }
    );
    watch(
      // 
      () => data.Ktotal,
      () => {
        ctx.emit('getKtotal', data.Ktotal);
      }
    );
    watch(
      // 
      () => data.longTextCatalog,
      () => {
        ctx.emit('getLongTextCatalog', data.longTextCatalog);
      },
      { deep: true }
    );
    watch(
      // 
      () => data.matchingCatalogue,
      () => {
        ctx.emit('getCatalogue', data.matchingCatalogue);
      },
      { deep: true }
    );

    // 
    onMounted(() => {
      generateText(explainDoc); //
      data.centerEle = document.getElementById('longCenter'); // dom(domdata.centerEle)
      copyTable(); //
    });
    // 
    const initContent = () => {
      let pageContentArr = [];
      swh1.forEach((item) => {
        pageContentArr.push(item.pageContent);
      });
      data.longTextContent = pageContentArr.join(''); // 
    };
    // initContent();
    // 
    const choosefile = (fileList) => {
      if (fileList) {
        const reader = new FileReader();
        reader.readAsText(fileList, 'UTF-8');
        reader.onload = function (e) {
          generateText(e.target.result);
        };
      }
    };
    // 1
    const generateText = (docText) => {
      data.longTextCatalog = [];
      // htmldiv_contentdiv_content
      const fileContent = docText.match(
        /<div id="div_content"(?:.*?)>([\s\S]*)<\/div>/
      );
      let delTitlemap; //
      if (fileContent) {
        // 
        generateCatalog(docText);
        // 1([\s\S]*?)[1][0]
        // 
        delTitlemap = fileContent[1].replace(
          /<div id="div_titlemap" style="display:none;">([\s\S]*)<\/div>/,
          ''
        );
      } else {
        //div_contenttxt
        console.log('div_content');
        // 
        const text = docText.replace(
          /<div id="div_titlemap" style="display:none;">([\s\S]*)<\/div>/,
          ''
        );
        // 
        const isHaveTag = new RegExp('<(?:.*?)>').test(text);
        // 
        if (isHaveTag) {
          delTitlemap = text; //
        } else {
          // 
          const textArr = text.split(new RegExp('\r\n\r\n'));
          let addTagText = ''; //p
          textArr.forEach((item) => {
            addTagText = addTagText.concat(`<p>${item}</p>`);
          });
          delTitlemap = addTagText; //
        }
      }
      // 
      data.longTextContent = delTitlemap;
      data.oldQueryKeyWordReg = ''; //
      data.matchingCatalogue = []; //
      // 
      setTimeout(() => {
        copyTable();
      }, 0);
    };
    // 1
    const generateCatalog = (docText) => {
      // 
      const fileCatalog = docText.matchAll(
        /<div level="(.*?)" tagetid="(.*?)" catalogtitle="(.*?)" ?>/g
      );
      for (const Catalog of fileCatalog) {
        const CatalogObj = {
          recursion: parseInt(Catalog[1]), //
          grade: Catalog[1], //
          id: Catalog[2], //id  tag_p_1
          text: Catalog[3], //
          children: [] //
        };
        // 
        recursionSetCatalog(data.longTextCatalog, CatalogObj);
      }
    };
    // 2
    const recursionSetCatalog = (tarObj, addObj) => {
      // 00
      if (data.catalogGrade > 0) {
        if (addObj.recursion > data.catalogGrade) return; //
      }

      // 1
      if (addObj.recursion === 1) {
        tarObj.push(addObj);
      } else {
        // 1-- 
        addObj.recursion--;
        //   children
        recursionSetCatalog(tarObj[tarObj.length - 1].children, addObj);
      }
    };
    // 
    const copyTable = () => {
      // table
      let tableEleAll = Array.from(document.getElementsByTagName('table'));
      // table
      tableEleAll.forEach((itemTable, index) => {
        // 
        itemTable.onmouseenter = function (e) {
          data.copyButton = document.createElement('button'); //
          data.copyButton.className = 'copy';
          data.copyButton.innerHTML = '';
          const currentEle = e.target; //
          currentEle.appendChild(data.copyButton); //
          // 
          data.copyButton.onclick = new Clipboard('.copy', {
            // target
            target: function () {
              //
              itemTable.removeChild(itemTable.lastChild);
              // tableword
              currentEle.width = '100%';
              return currentEle;
            }
          });
          //
          data.copyButton.onclick.on('success', function (e) {
            emptySelectText();
            // 
            myTips('success', '~');
            data.copyButton.onclick.destroy();
          });
          // 
          data.copyButton.onclick.on('error', function (e) {
            emptySelectText();
            // 
            myTips('error', '~');
            data.copyButton.onclick.destroy();
          });
        };
        // 
        itemTable.onmouseleave = function () {
          if (data.copyButton) {
            data.copyButton.onclick.destroy();
          }
          itemTable.removeChild(itemTable.lastChild);
        };
      });
    };
    // 
    const emptySelectText = () => {
      window.getSelection
        ? window.getSelection().removeAllRanges()
        : document.selection.empty();
    };
    // 
    const startQuery = () => {
      data.textCenter = data.centerEle.innerText; //()
      let absQueryKeyWord = data.queryKeyWord.trim(); //
      if (data.queryType === RETRIEVAL_TYPE.sentence) {
        //;;
        absQueryKeyWord = absQueryKeyWord.replace(
          new RegExp('[;]', 'g'),
          ' '
        );
      }
      console.log(absQueryKeyWord);
      if (absQueryKeyWord) {
        // 
        data.queryKeyWordArr = absQueryKeyWord
          .split(' ')
          .filter((item) => item.length); //
        data.queryKeyWordArr.sort((a, b) => b.length - a.length); //  [ , ]=> [ , ]
        data.queryKeyWordReg = data.queryKeyWordArr.join('|'); // | 
        // 
        if (
          data.oldQueryKeyWordReg === data.queryKeyWordReg &&
          data.oldQueryType === data.queryType
        )
          return;
        // 
        data.oldQueryType = data.queryType; //
        data.oldQueryKeyWordReg = data.queryKeyWordReg; //
        data.isLoading = true; //
        resetData('haveQuery'); // ,
        setTimeout(() => {
          console.time('');
          setHighlight(); // ,loading
          console.timeEnd('');
        }, 0);
      } else {
        // data.isLoading = true; //
        data.oldQueryKeyWordReg = ''; //
        resetData(); //  
      }
    };
    // 1
    const resetData = (isQuery) => {
      if (data.isHaveKey) {
        setTimeout(() => {
          console.time('');
          emptyAllHigh(); //   
          console.timeEnd('');
          data.oldKeyEle = null; //
          data.oldPEle = null; //
          data.paragraphClickHigh = []; //dom
          data.matchingCatalogue = []; //
          data.matchingCatalogueP = []; //
          data.keyWordIndex = []; //
          data.Knum = 0; //
          data.Ktotal = 0; //
          data.listNumId = 0; //
          data.isHaveKey = false; //
          if (isQuery) return;
          // 
          data.isLoading = false; //
        }, 0);
      }
    };
    //2 
    const emptyAllHigh = () => {
      data.centerEle.innerHTML = data.longTextContent; //
      copyTable(); //
    };
    //
    const switchHighlight = (type, index) => {
      // 1  type.target
      if (data.Ktotal < 2 && index !== 'firstSwitch' && !type.target ) return;
      // 
      if (type === 'next') {
        //
        data.Knum = data.Knum === data.Ktotal ? 1 : ++data.Knum;
      } else if (type === 'prev') {
        //
        data.Knum = data.Knum <= 1 ? data.Ktotal : --data.Knum;
      } else {
        //
        const targetEle = type.target.children[0] || type.target;
        // id
        data.Knum = parseInt(targetEle.id.split('_')[1]);
        //    
        if (!data.matchingCatalogueP.length) {
          clickPHigh(index);
        } else {
          // ()
          PieceLongHigh(type);
        }
      }
      // id
      clickPieceList();
    };
    // 
    const PieceLongHigh = (type) => {
      // 
      setTimeout(() => {
        emptyPieceLongHigh();
        // li()
        const targetLi = type.target.children.length
          ? type.target
          : type.target.parentNode;
        //
        const listLength = targetLi.innerText.length;
        // text   0bug 0 1
        const keyText =
          targetLi.childNodes[0].innerText || targetLi.childNodes[1].innerText;
        // 
        const listStart = targetLi.innerText.indexOf(keyText);
        // 
        const listEnd = listLength - listStart;
        // ****
        // 
        const targetEle = document.querySelector(`#key_${data.Knum}`);
        // id,
        const keyIndex = targetEle.parentNode.innerHTML.indexOf(
          `key_${data.Knum}`
        );
        // id
        const longStart = keyIndex - 25 - listStart;
        const longEnd = keyIndex + 89 + `${data.Knum}`.length + listEnd;
        // 
        const targetText = targetEle.parentNode.innerHTML.slice(
          longStart,
          longEnd
        );
        // 
        targetEle.parentNode.innerHTML = targetEle.parentNode.innerHTML.replace(
          targetText,
          `<span style="background-color:${data.keyWordBColRange};color:${data.keyWordSColRange}" >${targetText}</span>`
        );
        const newTargetEle = document.querySelector(`#key_${data.Knum}`);
        data.oldPieceTargetEle = newTargetEle.parentNode.parentNode;
        data.oldPieceTargetText = targetText;
        data.oldPieceNum = data.Knum;
      }, 0);
    };
    // 
    const emptyPieceLongHigh = () => {
      if (data.oldPieceTargetText) {
        // 
        data.oldPieceTargetEle.innerHTML =
          data.oldPieceTargetEle.innerHTML.replace(
            `<span style="background-color:${data.keyWordBColRange};color:${data.keyWordSColRange}">${data.oldPieceTargetText}</span>`,
            data.oldPieceTargetText
          );
        // 
        const oldargetEle = document.querySelector(`#key_${data.oldPieceNum}`);
        // id
        if (oldargetEle) {
          oldargetEle.style.backgroundColor = 'transparent'; // 
          oldargetEle.style.color = data.keyWordBCol; // 
        }
        data.oldPieceTargetText = ''; //
      }
    };
    // 
    const clickPHigh = (index) => {
      setTimeout(() => {
        let PEle;
        // 
        if (data.paragraphClickHigh.length) {
          // dom if
          PEle = data.paragraphClickHigh[index];
        } else {
          // temporarySpan if
          PEle = document.getElementsByClassName('temporarySpan')[index];
        }
        // 
        if (data.oldPEle) {
          data.oldPEle.style.backgroundColor = 'transparent'; // 
          data.oldPEle.style.color = '#000';
        }
        if (PEle) {
          PEle.style.backgroundColor = data.keyWordBColRange;
          PEle.style.color = data.keyWordSColRange;
          data.oldPEle = PEle;
        }
      }, 0);
    };
    //
    const clickPieceList = () => {
      // 
      if (data.oldPEle) {
        data.oldPEle.style.backgroundColor = 'transparent'; // 
        data.oldPEle.style.color = '#000';
      }
      // 
      emptyPieceLongHigh();
      //  
      let keyEle = document.querySelector(`#key_${data.Knum}`);
      // 
      if (data.oldKeyEle) {
        data.oldKeyEle.style.backgroundColor = 'transparent'; // 
        data.oldKeyEle.style.color = data.keyWordBCol; // 
      }
      // 
      keyEle.style.backgroundColor = data.keyWordBCol; // 
      keyEle.style.color = data.keyWordSCol; // 
      // 2
      const wrapNode = document.querySelector('.long-text-tpl'); //
      // 
      wrapNode.scrollTo({
        top: trueOffsetTop(keyEle) - 400, //
        behavior: 'smooth'
      });
      // ,
      data.oldKeyEle = keyEle;
    };
    // top
    const trueOffsetTop = (elm) => {
      let top = elm?.offsetTop;
      let parent = elm?.offsetParent; //
      while (parent !== null) {
        top += parent.offsetTop;
        parent = parent.offsetParent;
      }
      return top;
    };
    //&&
    const setHighlight = () => {
      // forDomGetHighFilter  forDomGetHigh 
      forDomGetHighFilter(data.centerEle);
      // 
      if (data.Ktotal) {
        data.isHaveKey = true; //
        //  
        console.time('');
        getQueryList();
        console.timeEnd('');
      } else {
        myTips('error', '~');
      }
      data.isLoading = false;
    };
    // k++ dom
    const forDomGetHighFilter = (root) => {
      let children = root.children;
      const oneEleText = []; //
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        for (let i = 0; i < children.length; i++) {
          //   true
          const bool = data.queryKeyWordArr.some((item) => {
            return children[i].innerText.includes(item);
          });
          // 
          if (bool) {
            // cataloguePcatalogueP1
            // data.matchingCatalogueP.push({
            //   content: children[i].innerText
            // });
            // =====
            oneEleText.push(children[i]);
          }
        }
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        // tr()
        children = tdSubsection(children);
        for (let i = 0; i < children.length; i++) {
          //   true
          const bool = data.queryKeyWordArr.every((item) => {
            return children[i].innerText.includes(item);
          });
          // 
          if (bool) {
            // k ,id
            if (data.queryType === RETRIEVAL_TYPE.paragraph) {
              data.paragraphClickHigh.push(children[i]);
              data.matchingCatalogue.push({
                content: children[i].innerText
              });
            }
            // 
            oneEleText.push(children[i]);
          }
        }
      }
      //  
      if (oneEleText.length) {
        oneEleText.forEach((item) => {
          forDomGetHigh(item);
        });
      }
    };
    //td
    const tdSubsection = (children) => {
      const tableAndChildren = [];
      for (let i = 0; i < children.length; i++) {
        if (children[i].nodeName === 'TABLE') {
          // tr
          const allTr = children[i].children[0].children;
          for (let t = 0; t < allTr.length; t++) {
            tableAndChildren.push(...allTr[t].children);
          }
        } else {
          tableAndChildren.push(children[i]);
        }
      }
      return tableAndChildren;
    };
    //******    dom
    const forDomGetHigh = (root) => {
      //===================  dom (100  k++6000ms-60ms
      for (let i = 0; i < root.childNodes.length; i++) {
        // 
        const currentNode = root.childNodes[i];
        // 
        const nodeText = currentNode.nodeValue;
        // console.log(!nodeText.includes('\n'));
        if (currentNode.nodeType == 1) {
          // 
          forDomGetHigh(currentNode);
        } else if (currentNode.nodeType == 3) {
          //    \n,table  data.queryType === RETRIEVAL_TYPE.sentence &&  !nodeText.includes('\n')
          //
          if (
            data.queryType === RETRIEVAL_TYPE.sentence &&
            filterSpace(nodeText)
          ) {
            // (  )
            withSentence(currentNode, nodeText);
            // returnbug
            return;
          } else {
            //   cataloguePcatalogueP2
            if (data.queryType === RETRIEVAL_TYPE.piece) {
              data.matchingCatalogueP.push({
                content: nodeText
              });
            }
            //  
            //     replace
            // 
            const highKeyStr = nodeTextHigh(nodeText);
            //
            // *****
            if (highKeyStr !== nodeText) {
              replaceNode(currentNode, highKeyStr);
            }
          }
        }
      }
      //===================

      //      
      //  dom
      /*  const children = root.children;
      // 
      if(children.length){
        for (let i = 0; i < children.length;i++){
         children[i].children&&forDomGetHigh(children[i])
        }
      }else {
          // KW: 
          // k1: 
          // 
          //         |   | | data.queryKeyWordReg
          root.innerHTML= root.innerText.replace(new RegExp(data.queryKeyWordReg, "g"), (KW) => {
              //
              data.Ktotal++;    //
              return `<span id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
          });
      } */
      // (html)
    };
    //
    const withSentence = (currentNode, nodeText) => {
      //   1:    aaaaaasahdddd;ccccc;hhhhsah
      const sentenceArr = nodeText.split(new RegExp('[;]')); //'[; \s]'  \s
      //   1: [aaaaaa,sahdddd ,hhhhsah]
      const satisfySentenceArr = sentenceArr.filter((itemSentence) => {
        // 
        if (!itemSentence) return false;
        // 
        const bool = data.queryKeyWordArr.every((item) => {
          return itemSentence.includes(item);
        });
        // 
        if (bool) return itemSentence;
      });
      // 
      if (!satisfySentenceArr.length) return;
      // 
      satisfySentenceArr.forEach((itemSen) => {
        // k
        data.matchingCatalogue.push({
          content: itemSen
        });
        // 
        const highKeyStr = nodeTextHigh(itemSen);
        // 
        // temporarySpan
        nodeText = nodeText.replace(
          itemSen,
          `<span class="temporarySpan">${highKeyStr}</span>`
        );
      });
      //1 innerHTML
      currentNode.parentNode.innerHTML = nodeText;
      //   2withSentence  span
      // withSentence
      // const withSentenceSpan = document.createElement('span');
      // withSentenceSpan.innerHTML = nodeText; //
      // withSentenceSpan.className = 'withSentence'; //class()
      // // 
      // currentNode.parentNode.replaceChild(withSentenceSpan, currentNode);
    };
    //
    const nodeTextHigh = (t) => {
      return t.replace(new RegExp(data.queryKeyWordReg, 'g'), (KW) => {
        data.Ktotal++; //
        // class="swhKey" 
        return `<span class="swhKey" id="key_${data.Ktotal}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`;
      });
    };
    // ()
    const replaceNode = (currentNode, highKeyStr) => {
      // spanhtml<span>
      const temporarySpan = document.createElement('span');
      temporarySpan.innerHTML = highKeyStr; //
      temporarySpan.className = 'temporarySpan'; //class()
      // 
      currentNode.parentNode.replaceChild(temporarySpan, currentNode);
      /**
       *  <p></p>       "" 
       * <p>
       *              temporarySpan
       *             <span class="temporarySpan">
       *               ""
       *              <span class="swhKey" id="key_1" style="color: rgb(255, 255, 255); padding: 0px 5px; background-color: red;"></span>
       *               ""
       *             </span>
       *            </p>
       *  */
    };

    //  
    const getQueryList = () => {
      // 
      if (data.queryType === RETRIEVAL_TYPE.piece) {
        // catalogueP
        data.matchingCatalogueP.length
          ? getOriginalPIndex()
          : getOriginalIndex();
      } else if (
        data.queryType === RETRIEVAL_TYPE.paragraph ||
        RETRIEVAL_TYPE.sentence
      ) {
        //    
        data.matchingCatalogue.forEach((item) => {
          item.content = pureListTextHigh(item.content); //
        });
      }
      // 
      switchHighlight('next', 'firstSwitch');
    };
    //
    const getOriginalPIndex = () => {
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //
      // 
      data.matchingCatalogueP.forEach((p) => {
        const regExp = p.content.matchAll(regInter);
        const grouping = []; //
        for (const item of regExp) {
          // 
          const keyPObj = {
            kWord: item[0], //
            index: item.index //
          };
          // 
          grouping.push(keyPObj); //
        }
        // 
        data.keyWordIndex.push(grouping);
      });
      // (DIndex,)
      data.keyWordIndex.forEach((group, DIndex) => {
        getHighIndex(group, DIndex);
      });
    };
    //  
    // 1  
    const getOriginalIndex = () => {
      // 
      const regInter = eval(`/${data.queryKeyWordReg}/g`); //
      const regExp = data.textCenter.matchAll(regInter); //
      for (const item of regExp) {
        const keyPObj = {
          kWord: item[0], //
          index: item.index //
        };
        data.keyWordIndex.push(keyPObj); //
      }
      // 30
      getHighIndex(data.keyWordIndex, 0);
    };
    // 2 30
    const getHighIndex = (KWArr, GIndex) => {
      let handleIndex = [];
      for (let i = 0; i < KWArr.length; i++) {
        // 0
        const prevIndex = i
          ? KWArr[i - 1].index + KWArr[i - 1].kWord.length
          : 0;
        const currentIndex = KWArr[i].index; //
        //,undefined 
        const nextIndex = KWArr[i + 1]?.index;
        //-30
        const initStartIndex = currentIndex - data.keyPLength;
        //  ++30
        const initEndIndex =
          currentIndex + KWArr[i].kWord.length + data.keyPLength;
        let handleIndexObj = {
          // 
          startIndex: initStartIndex < prevIndex ? prevIndex : initStartIndex,
          // 
          // undefined
          endIndex: initEndIndex > nextIndex ? nextIndex : initEndIndex
        };
        handleIndex.push(handleIndexObj); // 
      }
      highParagraphHandle(handleIndex, GIndex); // 
    };
    //     3  
    const highParagraphHandle = (SEindexObj, GIndex) => {
      SEindexObj.forEach((item, index) => {
        let pCenter = ''; //
        if (data.matchingCatalogueP.length) {
          // data.catalogueP
          pCenter = data.matchingCatalogueP[GIndex].content.slice(
            item.startIndex,
            item.endIndex
          );
        } else {
          //  
          //  
          pCenter = data.textCenter.slice(item.startIndex, item.endIndex);
        }
        // id
        const itemKeyWordObj = {
          content: pureListTextHigh(pCenter) //
        };
        // k 
        data.matchingCatalogue.push(itemKeyWordObj);
      });
    };
    //
    const pureListTextHigh = (itemContent) => {
      return itemContent.replace(
        new RegExp(data.queryKeyWordReg, 'g'),
        (KW, index) => {
          data.listNumId++;
          //  console.log(index);   
          return `<span id="list_${data.listNumId}" style="color:${data.keyWordBCol};padding:0 5px">${KW}</span>`; // 
        }
      );
    };
    // 
    const filterSpace = (str) => {
      return str.replace(/(^\s+)|(\s+$)|\s+/g, '');
    };
    // 
    const myTips = (type, text) => {
      proxy.h_tips({
        type: type, //success   error
        msg: text,
        top: 30,
        time: 1000
      });
    };
    return {
      data,
      startQuery,
      switchHighlight,
      choosefile
    };
  }
};
</script>

